import Vue from 'vue';

$.fn.addSVGClass = function (className) {
    var $svg = $(this);
    if ($svg.length > 0) {
        var svgEl = $svg[0];
        if (svgEl.classList) {
            svgEl.classList.add(className);
        } else {
            var className = $svg.attr('class') || '';
            $svg.attr('class', className + ' ' + className);
        }
    }
    return this;
};

$.fn.removeSVGClass = function (className) {
    var $svg = $(this);
    if ($svg.length > 0) {
        var svgEl = $svg[0];
        if (svgEl.classList) {
            svgEl.classList.remove(className);
        } else {
            $svg.attr('class', ($svg.attr('class') || '').replace(className, ' '));

        }
    }
    return this;
};

//https://developer.mozilla.org/zh-CN/docs/Web/JavaScript/Reference/Global_Objects/Array/findIndex
if (!Array.prototype.findIndex) {
    Array.prototype.findIndex = function(predicate) {
        if (this === null) {
            throw new TypeError('Array.prototype.findIndex called on null or undefined');
        }
        if (typeof predicate !== 'function') {
            throw new TypeError('predicate must be a function');
        }
        var list = Object(this);
        var length = list.length >>> 0;
        var thisArg = arguments[1];
        var value;

        for (var i = 0; i < length; i++) {
            value = list[i];
            if (predicate.call(thisArg, value, i, list)) {
                return i;
            }
        }
        return -1;
    };
}

//https://developer.mozilla.org/zh-CN/docs/Web/JavaScript/Reference/Global_Objects/Array/find
if (!Array.prototype.find) {
    Object.defineProperty(Array.prototype, 'find', {
        enumerable: false,
        configurable: true,
        writable: true,
        value: function(predicate) {
            if (this == null) {
                throw new TypeError('Array.prototype.find called on null or undefined');
            }
            if (typeof predicate !== 'function') {
                throw new TypeError('predicate must be a function');
            }
            var list = Object(this);
            var length = list.length >>> 0;
            var thisArg = arguments[1];
            var value;

            for (var i = 0; i < length; i++) {
                if (i in list) {
                    value = list[i];
                    if (predicate.call(thisArg, value, i, list)) {
                        return value;
                    }
                }
            }
            return undefined;
        }
    });
}


let escapeMap = {
    '&': '&amp;',
    '<': '&lt;',
    '>': '&gt;',
    '"': '&quot;',
    "'": '&#x27;',
    '`': '&#x60;'
};

let createEscaper = function(map) {
    let escaper = function(match) {
        return map[match];
    };

    let source = '(?:' + Object.keys(map).join('|') + ')';
    let testRegexp = RegExp(source);
    let replaceRegexp = RegExp(source, 'g');
    return function(string) {
        string = string == null ? '' : '' + string;
        return testRegexp.test(string) ? string.replace(replaceRegexp, escaper) : string;
    };
};

let utils = {
    escape: createEscaper(escapeMap),

    /**
     * Escapes the `RegExp` special characters "^", "$", "\", ".", "*", "+",
     * "?", "(", ")", "[", "]", "{", "}", and "|" in `string`.
     **/
    escapeRegExp: function(str){
        var reRegExpChar = /[\\^$.*+?()[\]{}|]/g;
        var reHasRegExpChar = RegExp(reRegExpChar.source);
        return (str && reHasRegExpChar.test(str))
            ? str.replace(reRegExpChar, '\\$&') : str;
    },
    
    parseJSON: function (objOrStr) {
        var ret;

        if (Vue.util.isObject(objOrStr)) {
            ret = objOrStr;
        } else {
            try {
                ret = JSON.parse(String(objOrStr));
            } catch (e) {
                ret = {};
            }
        }

        return ret;
    },

    debounce: function(func, wait, immediate) {
        var timeout, args, context, timestamp, result;

        var later = function() {
            var last = new Date().getTime() - timestamp;

            if (last < wait && last >= 0) {
                timeout = setTimeout(later, wait - last);
            } else {
                timeout = null;
                if (!immediate) {
                    result = func.apply(context, args);
                    if (!timeout) context = args = null;
                }
            }
        };

        return function() {
            context = this;
            args = arguments;
            timestamp = new Date().getTime();
            var callNow = immediate && !timeout;
            if (!timeout) timeout = setTimeout(later, wait);
            if (callNow) {
                result = func.apply(context, args);
                context = args = null;
            }

            return result;
        };
    },


};

export default utils;



