export default {
// like $(el).data(key)
  getElData: function (el, key) {
    const dataset = el.dataset;
    if (dataset) {
      return dataset[key];
    } else {
      return undefined;
    }
  },

  getElWidth(el) {
    const width = getComputedStyle(el).width.split('px')[0];
    if (isNaN(width)) {
      throw new Error({msg: 'width is not a number'});
    }
    return width;
  },
  setElWidth(el, width) {
    if (typeof width === 'string') {
        el.style.width = width;
    }
    else {
      el.style.width = width + 'px';
    }
  },

  getClosetEl(oriEl, selector) {
      let matchesFn;
      let parent;

      // Try to use original match method
      ['matches','webkitMatchesSelector','mozMatchesSelector','msMatchesSelector','oMatchesSelector'].some(function(fn) {
        if (typeof document.body[fn] == 'function') {
          matchesFn = fn;
          return true;
        }
        return false;
      });

    if (!matchesFn) {
    // polyfill match method for IE
      (function (ElementPrototype) {
        ElementPrototype.matches = function (selector) {
          var node = this, nodes = (node.parentNode || node.document).querySelectorAll(selector), i = -1;
          while (nodes[++i] && nodes[i] != node);
          return !!nodes[i];
        };
      })(Element.prototype);
    }
      // traverse parents
      while (oriEl!==null) {
        parent = oriEl.parentNode;
        if (parent!==null && parent[matchesFn](selector)) {
          return parent;
        }
        oriEl = parent;
      }

      return null;
  }
};



