<template>
    <span class="button-bar-selector">
        <template v-for="(button, index) in buttons">
            <span class="button-con" :class="{ 'first-button': index == 0, 'last-button' : index == buttons.length-1 }" :style="button.style">
                <lm-button type="toggle" :is-disabled="button.disabled" :is-active-at-init="button.selected" :data-button-value="button.value" :data-button-id="button.id" @toggle="onToggle">
                    {{button.name}}
                    <input type="text" v-if="button.type == 'input'" :value="button.value" @click="onClickInput" @keyup="onKeyUp" @blur="onBlurInput"/>
                </lm-button>
            </span>
        </template>
    </span>
</template>
<script>
    import Button from '../button/Button.vue';
    import dom from 'dom';
    import utils from 'utils';
    import './button-bar-selector.scss';
    let buttonId = 0;
    let ButtonBarSelector = {
        props: {
            buttonsAtInit: {
                type: [Array]
            },
            requireValue: {
                type: [Boolean],
                default: false
            }
        },
        data: function () {
            return {
                buttons: []
            }
        },
        components: {
            lmButton: Button
        },
        created () {
            let hasSelectedButton = false;
            this.buttonsAtInit.forEach(function (button) {
                if (button.disabled == null) {
                    button.disabled = false;
                }

                button.id = 'button_' + buttonId++;
                //When User data contains more than one buttons' selected is true, keep the first one selected is true.
                if (hasSelectedButton) {
                    button.selected = false;
                }
                if (button.selected) {
                    hasSelectedButton = true;
                }
            });
            this.buttons = this.buttonsAtInit;
        },
        methods: {
            onToggle (isActive, buttonView) {
                if (isActive) {
                    this.$children.forEach(function (buttonView) {
                        buttonView.isActive = false;
                    });
                    this.buttons.forEach(function (buttonData) {
                        buttonData.selected = false;
                    });
                    buttonView.isActive = true;
                } else {
                    if (this.requireValue) {
                        //if require value, can't toggle active button, only can select other button to un-selected current button
                        isActive = true;
                        buttonView.isActive = true;
                    } else {
                        buttonView.isActive = false;
                    }
                }
                let $button = $(buttonView.$el);
                let buttonId = $button.data('button-id');
                let value = $button.attr('data-button-value');
                let buttonData = this.buttons.find(function (button) {
                    return button.id == buttonId;
                });
                buttonData.selected = isActive;
                this.$emit('selected', isActive, value);

                if (isActive) {
                    let $input = $(buttonView.$el).find('input');
                    if ($input.length > 0) {
                        $input.focus();
                    }
                }
            },
            onClickInput (e) {
                //when input onclick the button wouldn't receive the click event popup by input, if the button is selected
                let buttonData = this._getButtonDataByInput(e.target);
                if (buttonData.selected) {
                    e.stopPropagation();
                }
            },
            onKeyUp (e) {
                let $input = $(e.target);
                let buttonData = this._getButtonDataByInput(e.target);
                buttonData.value = $input.val();
            },
            onBlurInput (e) {
                let buttonData = this._getButtonDataByInput(e.target);
                if (buttonData.selected) {
                    this.$emit('selected', buttonData.selected, buttonData.value);
                }
            },
            _getButtonDataByInput (input) {
                let $input = $(input);
                let $button = $input.closest('div');
                let buttonId = $button.data('button-id');
                let buttonData = this.buttons.find(function (button) {
                    return button.id == buttonId;
                });
                return buttonData;
            }
        }
    };
    export default ButtonBarSelector;
</script>



